import { styled } from '@stitches/react';
import { Button, Divider, Image, message, Modal } from 'antd';
import { useState } from 'react';
import CopyToClipboard from 'react-copy-to-clipboard';
import { ConfigsType } from '../configs';
import HongBao from './HongBao';

const isPortrait = window.matchMedia('(orientation: portrait)').matches;

const Section = styled('section', {
  background: '#EFEBE9',
  overflow: 'hidden',
  position: 'relative',
  padding: '0% 0% 5% 0%'
});

const Layout = styled('div', {
  width: '100%',
  padding: isPortrait ? '10% 0% 10% 5%' : '5% 0% 5% 10%',
});

const Title = styled('p', {
  color: '#795548',
  width: '100%',
  fontSize: isPortrait ? '2.5em' : '3.5em',
  margin: 0,
  fontWeight: '500',
});

const SubTitle = styled('p', {
  color: '#795548',
  width: '100%',
  fontSize: isPortrait ? '1.2em' : '2em',
  margin: '24px 0',
  fontWeight: '300',
  lineHeight: 1.8,
});

const GridLayout = styled('div', {
  display: 'grid',
  gridTemplateColumns: '50% 50%',
});

const Description = styled('p', {
  fontSize: '1em',
  lineHeight: 1.75,
  opacity: 0.65,
  marginTop: '8px',
});

type CongratulatoryMoneyProps = {
  config: ConfigsType;
};

const CongratulatoryMoney = ({ config }: CongratulatoryMoneyProps) => {
  const [groomVisible, setGroomVisible] = useState<boolean>(false);
  const [brideVisible, setBrideVisible] = useState<boolean>(false);

  return (
    <Section>
      <Layout>
        <Title>Gi li chc</Title>
        <SubTitle>Hy gi mt mn qu  by t li chc mng ca bn.</SubTitle>
      </Layout>
      <GridLayout>
        <HongBao title="Ch r" subTitle="Kim tra s ti khon" onClick={() => setGroomVisible(true)} />
        <HongBao title="C du" subTitle="Kim tra s ti khon" onClick={() => setBrideVisible(true)} />
      </GridLayout>
      <Modal
        title={<b>S ti khon ch r</b>}
        open={groomVisible}
        onOk={() => setGroomVisible(false)}
        onCancel={() => setGroomVisible(false)}
        cancelButtonProps={{ style: { display: 'none' } }}
        okButtonProps={{ style: { display: 'none' } }}
        footer={[<Description>Bm vo s ti khon  sao chp.</Description>]}
      >
        <div style={{ textAlign: 'center'}}>
          <div><b>Tn ch TK: NGUYEN VAN HUU</b></div>
          <CopyToClipboard text={'3656886867996'}>
            <Button
              type="text"
              onClick={() => message.success('S ti khon  c sao chp.')}
            >
              {'S TK: 3656886867996'}
            </Button>
          </CopyToClipboard>
          <div>Ngn hng TMCP Qun i (MBBank)</div>
          <Image src={config.qrHuu} preview={false} />
        </div>
      </Modal>
      <Modal
        title={<b>S ti khon c du</b>}
        open={brideVisible}
        onOk={() => setBrideVisible(false)}
        onCancel={() => setBrideVisible(false)}
        cancelButtonProps={{ style: { display: 'none' } }}
        okButtonProps={{ style: { display: 'none' } }}
        footer={[<Description>Bm vo s ti khon  sao chp.</Description>]}
      >
        <div style={{ textAlign: 'center'}}>
          <div><b>Tn ch TK: NGUYEN THI THAM</b></div>
          <CopyToClipboard text={'19034691755013'}>
            <Button
              type="text"
              onClick={() => message.success('S ti khon  c sao chp.')}
            >
              {'S TK: 19034691755013'}
            </Button>
          </CopyToClipboard>
          <div>Ngn hng TMCP K thng Vit Nam (Techcombank)</div>
          <Image src={config.qrTham} preview={false} />
        </div>
      </Modal>
    </Section>
  );
};

export default CongratulatoryMoney;
