import { useRef } from 'react';
import { styled } from '@stitches/react';
import useOnScreen from '../hooks/useOnScreen';
import { ConfigsType } from '../configs';
import { Col, Row } from 'antd';

const isPortrait = window.matchMedia('(orientation: portrait)').matches;

const Layout = styled('div', {
  width: '100%',
  padding: isPortrait ? '20% 0% 5% 5%' : '5% 5% 5% 5%',
});

const Title = styled('div', {
  color: '#795548',
  width: '100%',
  margin: 0,
  fontWeight: '500',
});

const SubTitle = styled('div', {
  color: '#795548',
  width: '100%',
  fontSize: isPortrait ? '1.2em' : '2em',
  margin: '24px 0',
  fontWeight: '300',
  lineHeight: 1.8,
});

type GreetingProps = {
  config: ConfigsType;
};

const Greeting = () => {
  const ref = useRef<HTMLSelectElement>(null);
  const onScreen: boolean = useOnScreen<HTMLDivElement>(ref, '-125px');

  return (
    <section
      ref={ref}
      style={{
        height: isPortrait ? '90vh' : '90vh',
        background: onScreen ? '#EFEBE9' : '#DADADA',
        overflow: 'hidden',
        position: 'relative',
        transition: 'background 1s ease-in',
      }}
    >
      <Layout>
        <Title style={{textAlign: 'center', fontSize: isPortrait ? '2.0em' : '3.5em'}} >
          <div>Trn trng knh mi</div>
          <div>Ti d <b>l thnh hn</b> ca chng ti</div>
        </Title>
        <SubTitle style={{textAlign: 'center'}}>
          <br/>
          <Row gutter={[16, 16]}>
            <Col span={isPortrait ? 23: 12}>
              <div style={{ border: '1px solid black', padding: '10px', borderRadius: '10px' }}>
                <Title style={{fontSize: '1.5em' }}>L thnh hn</Title>
                <div>Ngy 03 thng 12 nm 2023</div>
                <div>Ti nh trai lc: 10:00 sng</div>
                <div>Ti nh gi lc: 08:20 sng</div>
              </div>
            </Col>
            <Col span={isPortrait ? 23: 12}>
              <div style={{ border: '1px solid black', padding: '10px', borderRadius: '10px' }}>
                <Title style={{fontSize: '1.5em' }}>Tic ci</Title>
                <div>Ngy 03 thng 12 nm 2023</div>
                <div>Ti nh trai lc: 17:00 chiu</div>
                <div>Ti nh gi lc: 07:00 sng</div>
              </div>
            </Col>
          </Row>
          <div style={{padding: '20px'}}>Rt hn hnh c n tip!</div>
        </SubTitle>
      </Layout>
    </section>
  );
};

export default Greeting;
